import React, { useState } from "react"
import { Container, Card, CardTitle, CardText, Row, Col } from "reactstrap"

const Tools = props => {

  const [date, setDate] = useState('');
  const [personalYear, setPersonalYear] = useState('');
  const [personalMonth, setPersonalMonth] = useState('');
  const [personalDay, setPersonalDay] = useState('');

  const onchange = (e) => {
    const birthday = e.target.value;
    const [year, month, day] = birthday.split('-');
    const currentYear = new Date().getFullYear();
    // const currentYear = new Date().getFullYear();

    setPersonalYear(calc(year) + calc(currentYear));
    setPersonalMonth(calc(month));
    setPersonalDay(calc(day));
  }


  const calc = (n) => {
    return n.split('')
      .map(str => parseInt(str))
      .reduce((acc, d) => {
        return acc + d
      }, 0)
  }


  return (
    <React.Fragment>
      <div className="page-content">
        <Container fluid >
          <h4 className="">Tools</h4>

          <div className="mt-5">
            <Row>
              <Col sm="4 mx-auto">
                <input className="form-control" type="date" onChange={onchange} />
              </Col>
            </Row>
          </div>


          <div className="mt-5">

            <Row >
              <Col sm="4">
                <Card body inverse color="primary">
                  <CardTitle className="text-white text-center" tag="h5">Personal Year Number</CardTitle>
                  <CardText className="text-center display-4">
                    {personalYear}
                  </CardText>
                </Card>
              </Col>

              <Col sm="4">
                <Card body inverse color="success" >
                  <CardTitle className="text-white text-center" tag="h5">Personal Month Number</CardTitle>
                  <CardText className="text-center display-4">
                    {personalMonth}
                  </CardText>
                </Card>
              </Col>

              <Col sm="4">
                <Card body inverse color="danger">
                  <CardTitle className="text-white text-center" tag="h5">Personal Day Number</CardTitle>
                  <CardText className="text-center display-4">
                    {personalDay}
                  </CardText>
                </Card>
              </Col>
            </Row>

          </div>

        </Container>
      </div>
    </React.Fragment>
  )
}

export default Tools
